          <div v-else-if="characterOptions.length === 0" class="empty-text">Êú™ÊâæÂà∞ÂèØÁî®ÁöÑËßíËâ≤</div>
          <div v-else class="list-detail-layout">
            <div class="item-list">
              <button
                v-for="char in characterOptions"
                :key="char.value"
