                </div>
                <div class="detail-row">
                  <span class="detail-label">‰ø°ÊÅØ:</span>
                  <span class="detail-value">{{ getSelectedCharacterInfo.info || 'Êó†' }}</span>
                </div>
                <div class="detail-actions">
                  <button
                    class="toggle-btn"
                    :class="{ 'toggled-on': localCharacterSelections.get(selectedCharacter) }"
                    @click="handleToggleCharacter(selectedCharacter)"
                  >
                    {{ localCharacterSelections.get(selectedCharacter) ? 'Â∑≤ÂêØÁî®' : 'Â∑≤Á¶ÅÁî®' }}
                  </button>
                </div>
              </template>
              <div v-else class="detail-placeholder">ËØ∑ÈÄâÊã©‰∏Ä‰∏™ËßíËâ≤Êü•ÁúãËØ¶ÊÉÖ</div>
            </div>
          </div>
        </div>

        <!-- ‰∫ã‰ª∂ÂºÄÂÖ≥ (DLC - Events) -->
        <div v-show="activeTab === 'events'" class="control-group">
          <div v-if="isLoading" class="loading-text">Ê≠£Âú®Âä†ËΩΩ‰∫ã‰ª∂ÂàóË°®...</div>
          <div v-else-if="eventOptions.length === 0" class="empty-text">Êú™ÊâæÂà∞ÂèØÁî®ÁöÑ‰∫ã‰ª∂</div>
          <div v-else class="list-detail-layout">
            <div class="item-list">
              <button
                v-for="event in eventOptions"
                :key="event.eventKey"
                class="list-item"
                :class="{
                  'toggled-on': localEventSelections.get(event.eventKey),
                  selected: selectedEvent === event.eventKey,
                }"
                @click="selectedEvent = event.eventKey"
              >
                {{ event.label }}
              </button>
            </div>
            <div class="item-detail">
              <template v-if="selectedEvent && getSelectedEventInfo">
                <h3 class="detail-name">{{ getSelectedEventInfo.label }}</h3>
                <div class="detail-row">
                  <span class="detail-label">‰ΩúËÄÖ:</span>
                  <span class="detail-value">{{ getSelectedEventInfo.author || 'Êú™Áü•' }}</span>
                </div>
                <div class="detail-row">
                  <span class="detail-label">Â§ßÂ∞è:</span>
                  <span class="detail-value">{{ getSelectedEventInfo.entries.length }} ‰∏™Êù°ÁõÆ</span>
                </div>
                <div class="detail-row">
                  <span class="detail-label">‰ø°ÊÅØ:</span>
                  <span class="detail-value">{{ getSelectedEventInfo.info || 'Êó†' }}</span>
                </div>
                <div class="detail-actions">
                  <button
                    class="toggle-btn"
                    :class="{ 'toggled-on': localEventSelections.get(selectedEvent) }"
                    @click="handleToggleEvent(selectedEvent)"
                  >
                    {{ localEventSelections.get(selectedEvent) ? 'Â∑≤ÂêØÁî®' : 'Â∑≤Á¶ÅÁî®' }}
                  </button>
